.App {
  box-sizing: border-box;
  height: 100vh;
  padding: 5vmin;
  font-size: 1.2em;
  white-space: wrap;
  word-wrap: break-word;
  color: #cb2026;
  background: #0a0a07 url("./banner.png") no-repeat 0 100%;
  background-size: contain;
}

.App .App-title {
  position: absolute;
  bottom: 0;
  margin: 0;
  white-space: nowrap;
  text-align: left;
  font-size: calc(1em + 16vmax);
  opacity: .3;
}

.App .App-controls {
  text-align: center;
}

.App .App-main {
  text-align: right;
}

.App .App-controls > button {
  display: inline-block;
  width: 3em;
  height: 3em;
  margin-inline-end: 1em;
  background-color: transparent;
  border: 0 none;
  cursor: pointer;
}

.App-button-start {
  background-image: url("./images/icons/play.svg");
}
.App-button-pause {
  background-image: url("./images/icons/pause.svg");
}
.App-button-resume {
  background-image: url("./images/icons/eject.svg");
  transform: rotate(90deg);
}
.App-button-stop {
  background-image: url("./images/icons/stop.svg");
}
.App-button-lap {
  background-image: url("./images/icons/rotate.svg");
}
.App-button-clear {
  background-image: url("./images/icons/io.svg");
}

.App .App-main pre {
  line-height: 0;
  font-size: calc(20px + 5vmax);
}
